@import "project.less";
@import "smart-grid.less";

/* general >> */

* {
    font-family: 'PT Sans Caption', sans-serif;
}

.reset();

.wrapper {
    .wrapper();
}

a {
    text-decoration: none;
}

.title {
    font-size: 34px;
    text-transform: uppercase;
    font-weight: bold;
}

.btn-style {
        display: inline-block;
        font-size: 32px;
        font-weight: bold;
        width: 35%;
        padding: 10px 30px;
        position: relative;
        border: 3px solid rgb(24, 22, 1);
        color: rgb(24, 22, 1);
        transition: .4s;
        text-align: center;
        

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        margin: auto;
        border: 3px solid rgba(0,0,0,0);
        transition: .4s;
      }
      &:hover {
        padding-left: 5px;
        padding-top: 5px;
        
      }
      &:hover::after {
        border-color: rgb(24, 22, 1);
        // width: calc(100% - 10px);
        // height: calc(100% + 10px);
        margin-left: 3px;
        margin-top: 3px;
      }
}
/* << general */

/* Header >> */
.header {
    background-color: rgb(22, 22, 22);

    .nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;

        &__item {
            color: rgb(243, 248, 227);
            font-size: 32px;
            text-transform: uppercase;
            margin-left: 20px;
            padding: 15px 0;

            &:first-child {
                margin-left: 0;
            }
        }
    }
}
/* << Header */

/* Main >> */
/* About >> */
.about {
    .wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    
    padding-top: 60px;

    &__photo {
        width: 20%;
        height: 20%;
        box-shadow: 0px 0px 0px 5px #000, 0px 0px 0px 10px #fff;
    }

    .about-wrap-text {
        width: 70%;

        &__text {
            padding-top: 15px;
            font-size: 24px;

            &_resume {
                font-style: italic;

                a {
                    text-decoration: underline;
                    font-weight: bold;
                    color: darkcyan;
                }
            }
        }

        &__btn {
            margin-top: 80px;
            letter-spacing: 6px;
        }
    }
}
/* << About */

/* Skills >> */
.skills {
    margin-top: 80px;

    .skills-wrap {
        .row-flex();
        font-size: 24px;

        .skills-list {
            .size(6);
        }
    }
}
/* << Skills */

/* Portfolio >> */
.portfolio {
    margin-top: 80px;

    .portfolio-list {
        .row-flex();
        justify-content: space-between;
        margin-top: 30px;

        .portfolio-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-end;
            position: relative;
            border: 6px solid rgb(24, 22, 1);
            color: rgb(24, 22, 1);
            transition: .4s;
            padding: 20px 40px;
            width: 273px;

            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                margin: auto;
                border: 3px solid rgba(0,0,0,0);
                transition: .4s;
              }
              &:hover {
                padding-top: 5px;
                
              }

            &__note {
                margin-top: 30px;
                font-size: 24px;
                color: #000;
            }

            &__img {
                width: 273px;
                height: 437px;
            }
        }
    }
}
/* << Portfolio */
/* << Main */

/* Footer >> */
.footer {
    background-color: #000;
    margin-top: 100px;
    padding-bottom: 50px;

    &__item {
        font-size: 28px;
        text-align: center;
        color: #fff;
        padding-top: 10px;

        a {
            color: rgb(243, 248, 227);
            font-weight: bold;
        }
    }
}
/* << Footer */