/* general >> */

* {
  font-family: 'PT Sans Caption', sans-serif;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1200px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.title {
  font-size: 34px;
  text-transform: uppercase;
  font-weight: bold;
}

.btn-style {
  display: inline-block;
  font-size: 32px;
  font-weight: bold;
  width: 35%;
  padding: 10px 30px;
  position: relative;
  border: 3px solid #181601;
  color: #181601;
  transition: .4s;
  text-align: center;
}

.btn-style::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  margin: auto;
  border: 3px solid rgba(0, 0, 0, 0);
  transition: .4s;
}

.btn-style:hover {
  padding-left: 5px;
  padding-top: 5px;
}

.btn-style:hover::after {
  border-color: #181601;
  margin-left: 3px;
  margin-top: 3px;
}

/* << general */

/* Header >> */

.header {
  background-color: #161616;
}

.header .nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.header .nav__item {
  color: #f3f8e3;
  font-size: 32px;
  text-transform: uppercase;
  margin-left: 20px;
  padding: 15px 0;
}

.header .nav__item:first-child {
  margin-left: 0;
}

/* << Header */

/* Main >> */

/* About >> */

.about {
  padding-top: 60px;
}

.about .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.about__photo {
  width: 20%;
  height: 20%;
  box-shadow: 0px 0px 0px 5px #000, 0px 0px 0px 10px #fff;
}

.about .about-wrap-text {
  width: 70%;
}

.about .about-wrap-text__text {
  padding-top: 15px;
  font-size: 24px;
}

.about .about-wrap-text__text_resume {
  font-style: italic;
}

.about .about-wrap-text__text_resume a {
  text-decoration: underline;
  font-weight: bold;
  color: darkcyan;
}

.about .about-wrap-text__btn {
  margin-top: 80px;
  letter-spacing: 6px;
}

/* << About */

/* Skills >> */

.skills {
  margin-top: 80px;
}

.skills .skills-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  font-size: 24px;
}

.skills .skills-wrap .skills-list {
  width: calc(50% - 10px);
}

/* << Skills */

/* Portfolio >> */

.portfolio {
  margin-top: 80px;
}

.portfolio .portfolio-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
  margin-top: 30px;
}

.portfolio .portfolio-list .portfolio-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  border: 6px solid #181601;
  color: #181601;
  transition: .4s;
  padding: 20px 40px;
  width: 273px;
}

.portfolio .portfolio-list .portfolio-item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  margin: auto;
  border: 3px solid rgba(0, 0, 0, 0);
  transition: .4s;
}

.portfolio .portfolio-list .portfolio-item:hover {
  padding-top: 5px;
}

.portfolio .portfolio-list .portfolio-item__note {
  margin-top: 30px;
  font-size: 24px;
  color: #000;
}

.portfolio .portfolio-list .portfolio-item__img {
  width: 273px;
  height: 437px;
}

/* << Portfolio */

/* << Main */

/* Footer >> */

.footer {
  background-color: #000;
  margin-top: 100px;
  padding-bottom: 50px;
}

.footer__item {
  font-size: 28px;
  text-align: center;
  color: #fff;
  padding-top: 10px;
}

.footer__item a {
  color: #f3f8e3;
  font-weight: bold;
}

/* << Footer */

@media screen and (max-width: 981px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 748px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}